import React, { useRef, useState } from 'react';
import { Modal, Divider, message } from 'antd';

import { StsUploadImagesList } from '../upload_files/sts_upload_files_list';
import { GroupPurchaseCommanderRequestPost } from './group_purchase_commander_requests';
import { CustomForm } from '../base_components/form_component';
import { GroupPurchaseCommanderOptModal } from '../modals/group_purchase_commander_opt'

import {
  formKeys,
  formDisabledKeys,
  formNotShowKeys,
  formKeysLabelsObj,
  formKeySelectComponentsObj,
  formKeysHandleButtonObj,
  formGetRequestParamsKeys,
  formInitialObj,
  formRulesList,
  imageKeyNames,
  maxImagesNum
} from './group_purchase_commander_columns_label_config';

// 新建 Modal

const CreateGroupPurchaseCommander = ({ visible, setVisible, reloadListCount, setReloadListCount }) => {

  const groupPurchaseCommanderImageObj = useRef({});
  const oldObj = useRef({createCount: 0});

  const [initFormCount, setInitFormCount] = useState(0);

  const [postRequestCount, setPostRequestCount] = useState(0);
  const reqObject = useRef({});

  // 选择团购团长 modal
  const [updateFormObj, setUpdateFormObj] = useState({});
  const [groupPurchaseConnanderOptModalState, setGroupPurchaseConnanderOptModalState] = useState(false);
  const choseGroupPurchaseCommanderId = useRef(0);
  const selectGroupPurchaseConnanderHandle = (groupPurchaseCommanderObj) => {
    // 选择顾客操作
    // console.log(groupPurchaseCommanderObj);
    let showName = groupPurchaseCommanderObj.name;
    setUpdateFormObj({ group_purchase_commander_name: showName });
    if (groupPurchaseCommanderObj.id > 0) {
      choseGroupPurchaseCommanderId.current = groupPurchaseCommanderObj.id;
    }
    setGroupPurchaseConnanderOptModalState(false);
  };

  const postResponseDataHandle = (respObj) => {
    // 表单提交后的操作
    if (Object.keys(respObj).includes('code')
      && respObj.code === 1000) {
      closeModal();
      setReloadListCount(reloadListCount + 1)
    } else {
      message.error('网络错误...');
    }
  }

  const closeModal = () => {
    // 初始化表格和图片 并关闭Modal
    groupPurchaseCommanderImageObj.current = {};  // 清空图片数据
    oldObj.current = {createCount: oldObj.current.createCount + 1};  // 初始化图片
    setInitFormCount(initFormCount + 1);  // 初始化表格
    setVisible(false);
  }

  const groupPurchaseCommanderImageUploaded = (imagesObj) => {
    // 图片上传后操作
    groupPurchaseCommanderImageObj.current = { ...imagesObj };
  }

  const onFinish = (values) => {
    // 提交表单
    let upObj = { ...values, ...groupPurchaseCommanderImageObj.current };
    // let newDate = new Date(upObj.end_time);
    // upObj.end_time = parseInt(newDate.getTime());    // 时间转时间戳
    reqObject.current = upObj;
    setPostRequestCount(postRequestCount + 1);
  }

  return (
    <>
       <Modal visible={visible}
        title="GroupPurchaseCommander 新建"
        closable={false}
        footer={null}
      >
        <div style={{ width: '100%', height: 'auto', textAlign: 'center' }}>
          <StsUploadImagesList
            oldObj={oldObj.current}
            maxImagesNum={maxImagesNum}
            imageKeyName={imageKeyNames[0]}
            onChange={groupPurchaseCommanderImageUploaded}></StsUploadImagesList>
        </div>
        <Divider></Divider>

        <GroupPurchaseCommanderOptModal
          modalState={groupPurchaseConnanderOptModalState}
          setModalState={setGroupPurchaseConnanderOptModalState}
          selectHandle={selectGroupPurchaseConnanderHandle}
        ></GroupPurchaseCommanderOptModal>

        <CustomForm
          columns={formKeys}
          disabledKeys={formDisabledKeys}
          notShowKeys={formNotShowKeys}
          formKeyLabelObj={formKeysLabelsObj}
          formKeySelectComponentsObj={formKeySelectComponentsObj}
          formKeysItemButtonObj={formKeysHandleButtonObj(setGroupPurchaseConnanderOptModalState)}
          onFinish={onFinish}
          cancelHandle={closeModal}
          showDeleteButton={false}
          formInitialObj={formInitialObj}
          initFormCount={initFormCount}
          rulesObj={formRulesList}
          updateFormObj={updateFormObj}
        ></CustomForm>

      <GroupPurchaseCommanderRequestPost
        requestCount={postRequestCount}
        reqObject={reqObject.current}
        responseDataHandle={postResponseDataHandle}></GroupPurchaseCommanderRequestPost>
      </Modal>
    </>
  )
}

CreateGroupPurchaseCommander.defaultProps = {
  visible: false,
  setVisible: (val) => console.log(val),
  reloadListCount: 0,
  setReloadListCount: (booleanVal) => console.log(booleanVal)
}

export default CreateGroupPurchaseCommander;
