import React, { useState, useRef } from 'react';
// import { navigate, Link } from 'gatsby';
import { Button, Row, Col, Space, Tooltip } from 'antd';
import { ReloadOutlined, SearchOutlined } from '@ant-design/icons';

import CreateGroupPurchaseCommander from './create_group_purchase_commander';
import GroupPurchaseCommanderQueryDrawerForm from './group_purchase_commander_query_form';
import GroupPurchaseCommanderList from './group_purchase_commander_list';
import GroupPurchaseCommanderQueryParams from './group_purchase_commander_query_params_display';
// GroupPurchaseCommander 入口组件

const GroupPurchaseCommanderComponent = ({ selectButtonOnClick = null }) => {
  const reqParams = useRef({ page: 0 });

  const [reloadListCount, setReloadListCount] = useState(1);

  const [drawerVisible, setDrawerVisible] = useState(false);

  const [createGroupPurchaseCommanderVisible, setCreateGroupPurchaseCommanderVisible] = useState(false);

  const queryHandle = (currentRequestParams) => {
    // 查询操作
    reqParams.current = { ...currentRequestParams };
    // 触发重新读取数据
    setReloadListCount(reloadListCount + 1)
    // console.log('search...');
  };

  const initReloadList = () => {
    // 刷新列表, 删除所有查询条件
    reqParams.current = { page: 0 };
    setReloadListCount(reloadListCount + 1);
  }

  // const selectButtonOnClick = (item, index) => {
  // 列表选择按钮点击事件
  //   console.log(index, item);
  // }

  return (
    <div>
      <Row>
        <Col span={14}>
          <Space>
            {/* <Tooltip title="返回">
              <Link to="/logistic"><LeftOutlined /></Link>
            </Tooltip> */}
            <h4>GroupPurchaseCommander</h4>
            <Tooltip title="刷新">
              <Button onClick={initReloadList} shape="circle" icon={<ReloadOutlined />}></Button>
            </Tooltip>
            <Tooltip title="搜索">
              <Button onClick={() => setDrawerVisible(true)} shape="circle" icon={<SearchOutlined />}></Button>
            </Tooltip>
          </Space>
        </Col>
        <Col span={10}>
          <div style={{ width: '100%', height: 'auto' }}>
            {/* <Button style={{ float: 'right' }} type="link" onClick={() => navigate('/sendexpressaccount')}>快递帐号设置</Button> */}
            <Button style={{ float: 'right' }} type="link" onClick={() => setCreateGroupPurchaseCommanderVisible(true)}>新建</Button>
          </div>
        </Col>
      </Row>
      <Row>
        <Col span={24}><GroupPurchaseCommanderQueryParams paramsObj={reqParams.current}></GroupPurchaseCommanderQueryParams></Col>
      </Row>
      <GroupPurchaseCommanderQueryDrawerForm
        visible={drawerVisible}
        setVisible={setDrawerVisible}
        queryHandle={queryHandle}
      ></GroupPurchaseCommanderQueryDrawerForm>

      <GroupPurchaseCommanderList
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
        requestParams={reqParams.current}
        selectButtonOnClick={selectButtonOnClick}
      ></GroupPurchaseCommanderList>

      <CreateGroupPurchaseCommander
        visible={createGroupPurchaseCommanderVisible}
        setVisible={setCreateGroupPurchaseCommanderVisible}
        reloadListCount={reloadListCount}
        setReloadListCount={setReloadListCount}
      ></CreateGroupPurchaseCommander>
    </div>
  )
};

export default GroupPurchaseCommanderComponent;
