import React, { useRef, useState, useEffect } from 'react';
import { Modal, Divider, message } from 'antd';

// import StsUploadFile from '../../upload_files/sts_upload_file';
import { StsUploadImagesList } from '../upload_files/sts_upload_files_list';
import { GroupPurchaseCommanderRequestPut, GroupPurchaseCommanderRequestDelete  } from './group_purchase_commander_requests';
import { CustomForm } from '../base_components/form_component';

import {
  formKeys,
  formDisabledKeys,
  formNotShowKeys,
  formKeysLabelsObj,
  formKeySelectComponentsObj,
  formKeysIsImages,
  formGetRequestParamsKeys,
  formInitialObj,
  formRulesList,
  imageKeyNames,
  maxImagesNum
} from './group_purchase_commander_columns_label_config';

// 编辑 GroupPurchaseCommander

const UpdateGroupPurchaseCommander = ({ visible, setVisible, oldObj, reloadListCount, setReloadListCount }) => {
  const [oldGroupPurchaseCommanderHomeImages, setOldGroupPurchaseCommanderHomeImages] = useState([]);
  const groupPurchaseCommanderImageObj = useRef({});

  const [oldGroupPurchaseCommanderImageUrl, setOldGroupPurchaseCommanderImageUrl] = useState('');
  const [oldFormObj, setOldFormObj] = useState({});

  const [initFormCount, setInitFormCount] = useState(0);

  const [putRequestCount, setPutRequestCount] = useState(0);
  const reqObject = useRef({});
  const putResponseDataHandle = (respObj) => {
    if (Object.keys(respObj).includes('code')
      && respObj.code === 1000) {
      message.success('修改成功');
      setReloadListCount(reloadListCount + 1);
      setVisible(false);
    } else {
      message.error('网络错误...');
    }
  }

  const [deleteRequestCount, setDeleteRequestCount] = useState(0);
  const deleteGroupPurchaseCommander = () => {
    // 删除当前对象
    setDeleteRequestCount(deleteRequestCount + 1);
    setReloadListCount(reloadListCount + 1);
  };

  useEffect(() => {
    initImgAndForm();   // 初始化表格和图片
  }, [visible, oldObj]);

  const initImgAndForm = () => {
    // 初始化表格和图片
    setInitFormCount(initFormCount + 1);  // 初始化表格
    setOldFormObj({ ...oldObj });  // 给表格赋值

    // 生成图片列表
    let groupPurchaseCommanderHomeImgs = [];
    const imgKey = imageKeyNames[0];
    for (let index = 1; index < 10; index++) {
      const currentCosNameKey = imgKey + index;
      const currentUrlKey = currentCosNameKey + '_url';
      const currentCosName = oldObj[currentCosNameKey];
      const currentUrl = oldObj[currentUrlKey];
      if (typeof currentCosName === 'string' && currentCosName.length > 5) {
        groupPurchaseCommanderHomeImgs.push({
          cosNameKey: currentCosNameKey,
          cosName: currentCosName,
          urlKey: currentUrlKey,
          url: currentUrl
        });
      }
    }
    console.log(groupPurchaseCommanderHomeImgs);
    setOldGroupPurchaseCommanderHomeImages(groupPurchaseCommanderHomeImgs);   // 更新图片列表
  }

  const groupPurchaseCommanderImageUploaded = (imagesObj) => {
    // 轮播图上传后操作
    console.log(imagesObj);
    groupPurchaseCommanderImageObj.current = imagesObj;
  }

  const onFinish = (values) => {
    // 提交表单
    let upObj = { ...values, ...groupPurchaseCommanderImageObj.current };
    console.log(upObj);
    // 时间转时间戳
    // let newDate = new Date(upObj.end_time);
    // upObj.end_time = parseInt(newDate.getTime());    // 时间转时间戳
    // console.log(upObj.end_time);
    reqObject.current = upObj;
    setPutRequestCount(putRequestCount + 1);
  }

  return (
    <Modal visible={visible}
      title="GroupPurchaseCommander"
      closable={false}
      footer={null}
    >
      <div style={{width: '100%', height: 'auto', textAlign: 'center'}}>
        <StsUploadImagesList
          maxImagesNum={maxImagesNum}
          imageKeyName={imageKeyNames[0]}
          oldObj={oldObj}
          onChange={groupPurchaseCommanderImageUploaded}
        ></StsUploadImagesList>
      </div>
      <Divider></Divider>

      <CustomForm
        columns={formKeys}
        disabledKeys={formDisabledKeys}
        notShowKeys={formNotShowKeys}
        formKeyLabelObj={formKeysLabelsObj}
        formKeySelectComponentsObj={formKeySelectComponentsObj}
        onFinish={onFinish}
        cancelHandle={() => setVisible(false)}
        showDeleteButton={true}
        deleteHandle={deleteGroupPurchaseCommander}
        updateFormObj={oldFormObj}
        formInitialObj={{}}
        rulesObj={formRulesList}
        initFormCount={initFormCount}
      ></CustomForm>

      <GroupPurchaseCommanderRequestPut
        requestCount={putRequestCount}
        reqObject={reqObject.current}
        responseDataHandle={putResponseDataHandle}
        id={oldFormObj.id}></GroupPurchaseCommanderRequestPut>
      <GroupPurchaseCommanderRequestDelete
        requestCount={deleteRequestCount}
        responseDataHandle={(respObj) => setVisible(false)}
        id={oldFormObj.id}></GroupPurchaseCommanderRequestDelete>
    </Modal>
  )
}

UpdateGroupPurchaseCommander.defaultProps = {
  visible: false,
  setVisible: (val) => console.log(val),
  oldObj: {}
}

export default UpdateGroupPurchaseCommander;
