import React from 'react';

import AxiosRequest from '../apis';
import { apiLists } from './group_purchase_commander_columns_label_config';

// GroupPurchaseCommander 的request请求

const GroupPurchaseCommanderRequestGet = ({ requestCount, reqParams, responseDataHandle }) => {
  // GroupPurchaseCommander Get请求
  const api = apiLists[0];
  const method = 'get';
  const reqObject = {};

  return (
    <AxiosRequest
      responseDataHandle={responseDataHandle} api={api} method={method}
      reqParams={reqParams} reqObject={reqObject} requestCount={requestCount}
    ></AxiosRequest>
  );
};
GroupPurchaseCommanderRequestGet.defaultProps = {
  requestCount: 0,
  reqParams: {},
  responseDataHandle: (respObj) => { console.log(respObj); },
};

